import { Box, Stack, Typography } from "@mui/material";
import React from "react";
import Skillbar from "./Skillbar";
import ScrollAnimation from "../images/ScrollAnimation";

const Skills = () => {
  return (
    <Stack id="skills" alignContent="center" alignitems="center">
      <Typography
        variant="h2"
        sx={{
          pb: { md: 5, xs: 2 },
        }}
      >
        S K I L L S
      </Typography>
      <ScrollAnimation time={1}>
        <Box sx={{ width: "90vw" }}>
          <Skillbar idno={10} height={70} title={true} fwraps={false} />
        </Box>
      </ScrollAnimation>
    </Stack>
  );
};

export default Skills;
