import { useState } from "react";
import Skillbar from "./Skillbar";
import { productContanent } from "../images/image";
import ScrollAnimation from "../images/ScrollAnimation";
import { ExpandMoreOutlined } from "@mui/icons-material";
import github from "../images/icons8-github.svg";
import {
  Box,
  Card,
  CardActions,
  CardContent,
  CardMedia,
  Collapse,
  IconButton,
  Stack,
  Typography,
} from "@mui/material";

const Projects = () => {
  const [expandArray, setExpandArray] = useState(
    Array(Projects.length).fill(false)
  );
  const handleExpand = (index) => {
    const newexpandArray = [...expandArray];
    newexpandArray[index] = !newexpandArray[index];
    setExpandArray(newexpandArray);
  };

  return (
    <Box id="projects" textAlign="center">
      <Typography variant="h2" pb={2}>
        P R O J E C T S
      </Typography>

      <Stack
        direction="row"
        sx={{
          display: "flex",
          flexWrap: "wrap",
          overflowY: "auto",
          justifyContent: "center",
          alignItems: "center",
          alignContent: "center",
        }}
      >
        {productContanent.map((item, index) => (
          //------------ scroll animation
          <ScrollAnimation time={index} key={index}>
            <Card
              elevation={10}
              sx={{
                width: 300,
                m: { xs: 2 },
                p: 1.5,
                borderRadius: 5,
                position: "relative",
              }}
            >
              <CardMedia component="image" className="card_media">
                <img src={item.thumbnail} alt="project thumnail" />
              </CardMedia>
              <CardContent sx={{ p: 0, textAlign: "center" }}>
                <Typography variant="h6" color="secondary.main">
                  {item.title}
                </Typography>
                <a href={item.repo} target="_main">
                  <img
                    src={github}
                    alt="github"
                    className="socialIcon "
                    style={{
                      width: 40,
                      height: 50,
                      position: "absolute",
                      right: 8,
                      top: 1,
                      margin: 0,
                      cursor: "pointer",
                    }}
                  />
                </a>

                <Typography
                  sx={{ fontSize: { xs: 13 }, textAlign: "start", px: 2 }}
                >
                  {item.description}
                </Typography>

                <CardActions sx={{ justifyContent: "space-around" }}>
                  <a
                    href={item.site}
                    target="_main"
                    style={{
                      fontSize: 15,
                      fontWeight: "bold",
                      // translate: -60,
                      color: "green",
                    }}
                  >
                    #Visit-site
                  </a>
                  <IconButton
                    onClick={() => handleExpand(index)}
                    sx={{
                      "&:hover": {
                        backgroundColor: "#fcdc24a2",
                      },
                    }}
                  >
                    <ExpandMoreOutlined />
                  </IconButton>
                </CardActions>
                <Collapse in={expandArray[index]}>
                  <Typography
                    sx={{
                      fontSize: { xs: 13 },
                      textAlign: 'center',
                      px: 2,
                    }}
                  >
                    {item.details}
                  </Typography>
                </Collapse>

                <Skillbar
                  idno={item.idno}
                  height={30}
                  title={false}
                  fwraps={true}
                />
              </CardContent>
            </Card>
          </ScrollAnimation>
        ))}
      </Stack>
    </Box>
  );
};

export default Projects;
