import { Box, Paper, Stack, Typography } from "@mui/material";
import { imagelist } from "../images/image";

const Skillbar = ({ idno, height, title, fwraps }) => {
  return (
    <Stack
      direction="row"
      sx={{
        display: "flex",
        flexWrap: fwraps || "wrap",
        overflowY: "auto",
        justifyContent: "center",
        alignItems: "center",
        alignContent: "center",
      }}
    >
      {/* check transition group */}
      {imagelist.map(
        (item, index) =>
          index <= idno && (
            <Paper variant="outlined" className="skillbox " key={index}>
              <Box>
                <img src={item.icon} height={height} />
              </Box>
              {title === true && (
                <Typography sx={{ p: { md: 2, xs: 1 }, fontSize: 15 }}>
                  {item.name}
                </Typography>
              )}
            </Paper>
          )
      )}
    </Stack>
  );
};

export default Skillbar;
