import { Box, Paper, Slide, Typography } from "@mui/material";
import codebro from "../images/Code typing-bro.svg";
import SocialBlock from "./SocialBlock";

const About = () => {
  return (
    <Box id="about" sx={{ display: "flex", flexWrap:'wrap'}}>
      <Box
        sx={{
          display: { md: "flex", xs: "none" },
          position:'absolute',
          top:'15em',
          left:'1em',
          padding: 1,
        }}
      >
        <SocialBlock />
      </Box>
      
      <Slide in={true} timeout={600} direction="right">
        <Paper
          variant="none"
          sx={{
            p: { md: 2, xs: 2 },
            width: { md: "48vw", xs: "90vw" },
          }}
        >
          
          <Typography
            sx={{
              fontSize: { ls: 39, md: 35,sm:35, xs: 30 },
              p: { xs: 0.2 },
              textAlign: { md: "start", xs: "center" },
            }}
          >
            <span className="text">I'AM RAKESH</span>
            <br />
            Front-end Developer
          </Typography>
          <Typography
            textAlign="center"
            sx={{
              fontSize: { ls: 20, md: 20,sm:20, xs: 16 },
              py: { md: 3, xs: 2 },
              fontFamily: "Times New Roman, Times, serif",             
            }}
          >
            Welcome to my portfolio! I am a skilled front-end developer with a
            passion for creating immersive and engaging web experiences. With a
            keen eye for design and a strong expertise in HTML, CSS, and
            JavaScript, I bring websites to life, ensuring they not only look
            stunning but also function seamlessly.
          </Typography>
        </Paper>
      </Slide>
      <Box ml={{ ls: 8, md: 8, xs: 0 }}>
        <img src={codebro} alt="codeBro" height={400} />
      </Box>
    </Box>
  );
};

export default About;
