import "./index.css";
import { BrowserRouter } from "react-router-dom";
import { Navbar, About, Skills, Projects, Contacts } from "./components";
import {
  Container,
  CssBaseline,
  ThemeProvider,
  createTheme,
  responsiveFontSizes,
} from "@mui/material";
import { useState } from "react";

function App() {
  //  -------------- Creating Custom theme --------------
  const [darkmode, setDarkmode] = useState(false);

  const handletheme = () => {
    setDarkmode((prevMode) => !prevMode);
  };

  let theme = createTheme({
    palette: {
      mode: darkmode ? "dark" : "light",
      secondary: {
        main: darkmode ? "#fcdb24f3" : "#000000",
        light: darkmode ? "#fcdb24f3" : "#ffffffd7",
        dark: darkmode ? "#f56403 " : "#000000",
      },
    },
    typography: {
      h2: {
        fontSize: 40,
        fontFamily: "Franklin Gothic Medium, Arial Narrow, Arial, sans-serif",
      },
    },
  });

  theme = responsiveFontSizes(theme);
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <BrowserRouter>
        <Navbar handletheme={handletheme} />
        <Container overflow="hidden">
          <About />
          <Skills />
          <Projects />
        </Container>
        <Contacts />
      </BrowserRouter>
    </ThemeProvider>
  );
}

export default App;
