import { Paper, Stack } from "@mui/material";
import React from "react";
import { socialIcons } from "../images/image";

const SocialBlock = () => {
  return (
    <Stack direction="column" className="SocialBlock">
      {socialIcons.map((item, index) => (
        <Paper
          elevation={4}
          className="socialIcon"
          key={index}
          sx={{
            outline: " 1px solid #b39908c7",
           
          }}
        >
          <a href={item.link} target="_main">
            <img
              src={item.icon}
              alt="html"
              height={35}
              style={{
                display: "block",
                height: "35px",
                width: "35px",
              }}
            />
          </a>
        </Paper>
      ))}
    </Stack>
  );
};

export default SocialBlock;
