import React from "react";
import { Box, Typography } from "@mui/material";
import Forms from "./Forms";
import ScrollAnimation from "../images/ScrollAnimation";
import design from "../images/design 2.png";
import SocialBlock from "./SocialBlock";

const Contacts = () => {
  return (
    <Box id="contacts" px={2} textAlign="center">
      <Typography variant="h2" textAlign="center" p={5}>
        C O N T A C T
      </Typography>
      <ScrollAnimation time={1}>
        <Box
          sx={{
            display: "flex",
            justifyContent: "center",
          }}
        >
          <Forms />
          <Box ml={3}>
            <SocialBlock />
          </Box>
        </Box>
      </ScrollAnimation>
      <img src={design} alt="design patter" width="100%" />
    </Box>
  );
};

export default Contacts;
