import { Box, Button, Typography } from "@mui/material";
import React, { useState } from "react";
import { HashLink as Link } from "react-router-hash-link";
import DrawerB from "./DrawerB";
import { destination } from "../images/image";
import { DarkMode, LightMode } from "@mui/icons-material";

const Navbar = ({ handletheme }) => {
  // -------------- Toggling the mode icon -------

  const [mode, setMode] = useState(true);
  const modeHandler = () => {
    setMode((prevMode) => !prevMode);
    handletheme();
  };

  return (
    <Box
      className="navbar"
      sx={{
        px: { md: 9,sm:3, xs: 0.5 },
        py: 4,
        backgroundColor: "secondary.light",
        backdropFilter: "blur(5px)",
      }}
    >
      <Box>
        <Link to="/" smooth>
          <Typography
            sx={{
              fontSize: 40,
              fontFamily:
                "Franklin Gothic Medium, Arial Narrow, Arial, sans-serif",
            }}
          >
            RAKESH
          </Typography>
        </Link>
      </Box>
      <Box sx={{ display: "flex" }}>
        <Box className="linkTab" sx={{ display: "flex", alignItems: "center" }}>
          <Button
            size="small"
            onClick={() => modeHandler()}
            sx={{ color: "black", overflow: "hidden" }}
          >
            {mode ? <DarkMode /> : <LightMode />}
          </Button>

          {destination.map((item, index) => (
            <Link to={item.to} smooth key={index}>
              <Typography variant="body1" pl={3} sx={{fontFamily:"Fauna One"}}>
                {item.name}
              </Typography>
            </Link>
          ))}
        </Box>
        <DrawerB />
      </Box>
    </Box>
  );
};

export default Navbar;
