import { Box, Drawer, Paper, Typography } from "@mui/material";
import { HashLink as Link } from "react-router-hash-link";
import React, { useState } from "react";
import menueIcon from "../images/icons8-menu.svg";
import { destination } from "../images/image";

const DrawerB = () => {
  const [isDraweropen, setIsDraweropen] = useState(false);

  return (
    <>
      <Drawer
        className="drawer"
        anchor="right"
        open={isDraweropen}
        onClose={() => setIsDraweropen(false)}
      >
        <Box p={2} textAlign="center">
          {destination.map((item, index) => (
            <Link to={item.to} smooth key={index}>
              <Typography
                component={Paper}
                variant="h6"
                sx={{
                  backgroundColor: "secondary.light",
                  color: "black",
                }}
                p={2}
                m={2}
                onClick={() => setIsDraweropen((prevDrawer) => !prevDrawer)}
              >
                {item.name}
              </Typography>
            </Link>
          ))}
        </Box>
      </Drawer>

      <button
        className="drawer"
        onClick={() => setIsDraweropen((prevDrawer) => !prevDrawer)}
      >
        <img src={menueIcon} alt="menueIcon" />
      </button>
    </>
  );
};

export default DrawerB;
